/* src/components/Footer.css */

.Footer {
    background-color: #1A4A5A; /* Cor do fundo do rodapé */
    color: #fff; /* Cor do texto */
    text-align: center;
    padding: 20px;
    position: relative;
    width: 100%;
    bottom: 0;
    margin-top: 20px; /* Espaço acima do rodapé para evitar sobreposição */
}

.Footer-signature {
    font-style: italic; /* Define o texto como itálico */
    font-size: 14px; /* Ajusta o tamanho da fonte, se necessário */
}
